<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <img v-bind:src = imgSrc style="display:block; margin:auto">
    <Btns v-for="(coins, index) in AllCoins" :key="`coin-${index}`" v-bind:coin="coins"></Btns>
    <br>
    <p>{{inputText}}</p>
    <BasicInputText v:model="inputText" placeholder="Add a cryptocurrency" @keydown.enter="addCoin" />
    <button v-on:click="addCoin">Add Coin</button>
  </div>
</template>

<script>
import logo from '../assets/logo.png'
import Btns from '../components/CryptoButton'
import Coins from '../components/Coins'
import BasicInputText from '../components/BasicInputText'
// import { serverBus } from '../main'

export default {
  name: 'Cryptos',
  data () {
    return {
      msg: 'Welcome Max',
      imgSrc: logo,
      inputText: 'a',
      AllCoins: ['Bitcoin', 'Ethereum', 'Eos']
    }
  },
  components: {
    Btns,
    Coins,
    BasicInputText
  },
  methods: {
    addCoin: function () {
      console.log(this.inputText)
      // serverBus.$emit('sendCoins', this.AllCoins)
    }
  }
}
</script>



